.ContainerTreinamento {
    width: 100%;
    min-height: calc(100vh - 80px);
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    margin-left: 1%;
    margin-top: -10px;
  }
  

  .ContainerTreinamento .ContainerConteudo {
    width: 90vw;
    min-height: 800px;
    background-color: rgb(250, 250, 250);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin: 10px;
    padding: 20px;
    border-radius: 15px;
  }
  
  .ContainerConteudo .ContainerHeader {
    display: flex;
    justify-content: start;
    width: 100%;
  }
  
  .ContainerHeader .ContainerTitulo {
    margin-top: 1%;
    flex: 50%;
  }
