/* INICIO ESTILO SIDEBAR */
.barratoggle{
    display: flex;    
    z-index: 1;
  }
  
  .sidebar{
    background: linear-gradient(180deg, #803ca8, #fc6a03);
    color: #fff;
    height: 100vh;
    width: 220px;
    transition: all 0.5s;
    border-radius: 7px;
    margin-left: 0.3rem;
    margin-top: 0.3rem;
  }
  
  .top_selection{
    display: flex;
    align-items: center;
    padding: 20px 15px;
  }
  
  .logo{
    font-size:30px;  
  }

.logo img {
  height: 100px;
  width: 150px;
  margin: 60% 0 0 8%;
  transition: all 0.5s;
}

  
  .bars{
    display: flex;
    font-size: 25px;
    margin-left: 50px;
    transition: all 0.5s;
  }
  
  .link{
    display: flex;
    color:#fff;
    padding: 10px 15px;
    grid-gap: 15px;
    gap: 15px;
    transition: all 0.5s;
    text-decoration: none;
  }
  
  .link:hover{
    background-color: lightskyblue;
    color: #000;
    transition: all 0.5s;
  }
  
  .fabars{
    cursor: pointer;
  }
  
  .icon, .link_text{
    font-size: 17px;
  }
  
  
