body.active-modal {
    overflow-y: hidden;
}

.btn-modal {
    display: block;
    margin: 10px;
    font-size: 18px;
    cursor: pointer;
}

.btn-modal:hover{
    background-color: aqua;
}

.modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
}
.overlay {
    background: rgba(49,49,49,0.8);
}

.modal-content {
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    line-height: 1.4;
    background: #f1f1f1;
    padding: 5px 15px;
    border-radius: 3px;
    max-width: 500px;
    min-width: 300px;
    margin-top: 50px;
    border-radius: 10px;
}

.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
    cursor: pointer;
}

.close-modal:hover {
   color: aqua;
}



