.ContainerHome{
   display: flex;
   align-content: center;
}

.GridAvisosImportante {
   width: 600px;
   height: 300px;
   background-color: rgb(250, 250, 250);
   box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   margin: 10px auto 0 30px;
   padding: 20px;
   border-radius: 15px;
 }

 .GridAvisosImportante h6 {
   text-align: end;
   margin-top: 150px;
}

.Grid2 {   
   flex-wrap: wrap;
   width: 600px;
   height: 300px;
   background-color: rgb(250, 250, 250);
   box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   margin: 10px auto 0 30px;
   padding: 20px;
   border-radius: 15px;
 }

 .Grid2 h6 {
   text-align: end;
   margin-top: 150px;
}

 .GridSlide{
   width: 540px;
   height: 1000px;
   background-color: rgb(250, 250, 250);
   box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
   margin: 10px 0 0 35px;
   padding: 20px;
   border-radius: 15px;
   right: auto;
 }  
 
